.daily-alerts {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}

.daily-alerts-title {
  color: #0049B7;
  margin-bottom: 1rem;
}

.daily-alerts-items {
  display: flex;
  /* box-shadow: 0 1rem 5rem rgba(0, 0, 0, 0.2); */
}

.daily-alerts-item {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 6rem;
  border: 1px solid rgba(0, 0, 0, .12);
  margin-right: 1rem;
  color: #fff;
  border-radius: 5px;
}

.daily-alerts-item:nth-child(4) {
  margin-right: 0;
}

.daily-alerts-item h3 {
  margin-bottom: 1rem;
  font-size: 1.3rem;
}

.daily-alerts-item span {
  font-size: 1.2rem;
  font-weight: bold;
}